
import { computed, defineComponent } from "vue";
export default defineComponent({
  props: {
    bgColor: String,
    title: {
      type: String,
      default: "",
    },
  },
  setup(props) {
    const titleCom = computed(() => {
      if (props.title != "") {
        return props.title;
      } else {
        return props.bgColor;
      }
    });
    const frontBg = computed(() => {
      return {
        backgroundPosition: "bottom",
        backgroundImage: `linear-gradient(to top,${props.bgColor},transparent)`,
      };
    });
    const rightBg = computed(() => {
      return {
        backgroundPosition: "right",
        backgroundImage: `linear-gradient(to left,${props.bgColor},transparent)`,
      };
    });

    return { titleCom, frontBg, rightBg };
  },
});
