
import { computed, defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    title: {
      type: String,
      default: "Read More",
    },
    color: {
      type: String,
      default: "#e74c3c",
    },
  },
  setup(props) {
    const styleObj = computed(() => {
      return {
        background: props.color,
        boxShadow: `0 0 5px ${props.color}, 0 0 15px ${props.color}, 0 0 30px ${props.color},0 0 60px ${props.color}`,
      };
    });

    return { styleObj };
  },
});
