
import { computed, defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    color: {
      default: "red",
      type: String,
    },
    number: {
      default: 0.4,
      type: Number,
    },
  },
  setup(props) {
    const dash = computed(() => {
      return props.number * 100;
    });
    return { dash };
  },
});
