
import { defineComponent, onMounted, ref } from "vue";
export default defineComponent({
  setup() {
    const totalTime = 7500;
    const breathTime = (totalTime / 5) * 2;
    const holdOnTime = totalTime / 5;

    const text = ref("吸气");
    const isGrow = ref(true);

    function breath() {
      isGrow.value = true;
      setTimeout(() => {
        text.value = "HoldOn";
      }, breathTime);
      setTimeout(() => {
        text.value = "呼气";
        isGrow.value = false;
      }, breathTime + holdOnTime);
    }
    function cycle() {
      breath();
      setInterval(() => {
        breath();
      }, totalTime);
    }

    onMounted(() => {
      cycle();
    });

    return { text, isGrow };
  },
});
