
import {
  computed,
  defineComponent,
  onMounted,
  reactive,
  watch,
  toRefs,
} from "vue";
export default defineComponent({
  setup() {
    //一个小格6度
    const deg = 6;
    const time: any = reactive({
      hh: 0,
      mm: 0,
      ss: 0,
      hhRotate: computed(() => {
        return {
          transform: `rotateZ(${time.hh * deg * 5}deg)`,
        };
      }),
      mmRotate: computed(() => {
        return {
          transform: `rotateZ(${time.mm * deg}deg)`,
        };
      }),
      ssRotate: computed(() => {
        return {
          transform: `rotateZ(${time.ss * deg}deg)`,
        };
      }),
    });

    function update() {
      const day = new Date();
      time.hh = day.getHours() % 12;
      time.mm = day.getMinutes();
      time.ss = day.getSeconds();
      setInterval(() => {
        time.ss += 1;
      }, 1000);
    }

    watch(
      () => time.ss,
      (ss) => {
        if (ss % 60 == 0) time.mm += 1;
      }
    );
    watch(
      () => time.mm,
      (mm) => {
        if (mm % 60 == 0) time.hh += 1;
      }
    );

    onMounted(() => {
      update();
    });

    return { ...toRefs(time) };
  },
});
